"use client"

import { useState } from 'react'
import { Card } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Button } from '@/components/ui/button'
import { Label } from '@/components/ui/label'
import { Upload, X, Plus, Trash2, ImagePlus } from 'lucide-react'

type InvimaType = 'RS' | 'PC' | 'NR'
type RiskClass = 'I' | 'IIA' | 'IIB' | 'III'
type AcquisitionForm = 'Compra directa' | 'Donación' | 'Comodato' | 'Alquiler' | 'Otro'
type MetrologicalFrequency = 'Anual' | 'Semestral' | 'Otro'
type MetrologicalActivity = 'MP' | 'MC' | 'C'

interface MetrologicalHistoryRecord {
  date: string
  reportNumber: string
  activity: MetrologicalActivity[]
  supplier: string
  observations: string
}

interface ObservationRecord {
  date: string
  name: string
  observation: string
}

export default function AddEquipmentPage() {
  const [image, setImage] = useState<File | null>(null)
  const [imagePreview, setImagePreview] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    // Identificación
    name: '',
    inventoryCode: '',
    ipsCode: '',
    ecriCode: '',
    brand: '',
    model: '',
    serialNumber: '',
    manufactureDate: '',
    usefulLife: '',
    area: '',
    responsible: '',
    location: '',
    invimaType: null as InvimaType | null,
    invimaCode: '',
    riskClass: '' as RiskClass | '',
    components: '',
    
    // Registro histórico
    acquisitionForm: '' as AcquisitionForm | '',
    documentType: '',
    documentNumber: '',
    acquisitionDate: '',
    warrantyReplacementDate: '',
    warrantyExpiration: '',
    supplier: '',
    supplierNIT: '',
    supplierContact: '',
    
    // Condiciones de funcionamiento
    voltage: '',
    current: '',
    relativeHumidity: '',
    temperature: '',
    dimensions: '',
    weight: '',
    otherConditions: '',
    
    // Datos metrológicos
    measurementMagnitude: '',
    equipmentRange: '',
    serviceUsageRange: '',
    resolution: '',
    maxAllowedError: '',
    metrologicalFrequencyOption: '' as MetrologicalFrequency | '',
    metrologicalFrequencyOther: '',
    requiresCalibration: false,
    
    // Registros históricos metrológicos
    metrologicalHistory: [] as MetrologicalHistoryRecord[],
    
    // Observaciones
    observations: [] as ObservationRecord[],
  })

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      setImage(file)
      const reader = new FileReader()
      reader.onloadend = () => {
        setImagePreview(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const removeImage = () => {
    setImage(null)
    setImagePreview(null)
  }

  const addHistoryRecord = () => {
    setFormData(prev => ({
      ...prev,
      metrologicalHistory: [
        ...prev.metrologicalHistory,
        { date: '', reportNumber: '', activity: [], supplier: '', observations: '' }
      ]
    }))
  }

  const removeHistoryRecord = (index: number) => {
    setFormData(prev => ({
      ...prev,
      metrologicalHistory: prev.metrologicalHistory.filter((_, i) => i !== index)
    }))
  }

  const toggleHistoryActivity = (index: number, activity: MetrologicalActivity) => {
    setFormData(prev => {
      const newHistory = [...prev.metrologicalHistory]
      const record = newHistory[index]
      if (record) {
        const activityIndex = record.activity.indexOf(activity)
        if (activityIndex > -1) {
          record.activity.splice(activityIndex, 1)
        } else {
          record.activity.push(activity)
        }
        newHistory[index] = record
      }
      return { ...prev, metrologicalHistory: newHistory }
    })
  }

  const updateHistoryRecord = (index: number, field: keyof MetrologicalHistoryRecord, value: string) => {
    setFormData(prev => {
      const newHistory = [...prev.metrologicalHistory]
      if (newHistory[index]) {
        newHistory[index] = { ...newHistory[index], [field]: value }
      }
      return { ...prev, metrologicalHistory: newHistory }
    })
  }

  const addObservation = () => {
    setFormData(prev => ({
      ...prev,
      observations: [...prev.observations, { date: '', name: '', observation: '' }]
    }))
  }

  const removeObservation = (index: number) => {
    setFormData(prev => ({
      ...prev,
      observations: prev.observations.filter((_, i) => i !== index)
    }))
  }

  const updateObservation = (index: number, field: keyof ObservationRecord, value: string) => {
    setFormData(prev => {
      const newObservations = [...prev.observations]
      if (newObservations[index]) {
        newObservations[index] = { ...newObservations[index], [field]: value }
      }
      return { ...prev, observations: newObservations }
    })
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log('Form data:', { ...formData, image })
  }

  return (
    <div className="mx-auto max-w-7xl space-y-8">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold text-slate-900">Ingresar equipo</h1>
        <p className="text-slate-600">
          Completa la información del nuevo equipo biomédico
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Imagen del equipo */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <h2 className="mb-6 text-xl font-semibold text-slate-900">Imagen del equipo</h2>
          
          <div className="flex flex-col items-center gap-6 sm:flex-row">
            <label htmlFor="image-upload" className="cursor-pointer group">
              {imagePreview ? (
                <div className="relative">
                  <img
                    src={imagePreview}
                    alt="Vista previa"
                    className="h-40 w-40 rounded-2xl object-cover border-2 border-slate-200 transition-all group-hover:border-emerald-400"
                  />
                  <button
                    type="button"
                    onClick={(e) => {
                      e.preventDefault()
                      removeImage()
                    }}
                    className="absolute -top-3 -right-3 h-8 w-8 rounded-full bg-red-500 text-white flex items-center justify-center hover:bg-red-600 transition shadow-lg"
                  >
                    <X className="h-4 w-4" />
                  </button>
                </div>
              ) : (
                <div className="h-40 w-40 rounded-2xl border-2 border-dashed border-slate-300 flex flex-col items-center justify-center bg-slate-50 transition-all group-hover:border-emerald-400 group-hover:bg-emerald-50/50">
                  <ImagePlus className="h-10 w-10 text-slate-400 mb-2 transition-colors group-hover:text-emerald-600" />
                  <span className="text-sm font-medium text-slate-500 group-hover:text-emerald-700">Subir imagen</span>
                </div>
              )}
              <input
                id="image-upload"
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                className="hidden"
              />
            </label>
            
            <div className="flex-1 space-y-2">
              <p className="text-sm font-medium text-slate-700">
                Carga una foto del equipo para identificación visual
              </p>
              <p className="text-xs text-slate-500">
                Formatos: JPG, PNG, WebP • Tamaño máximo: 5MB
              </p>
            </div>
          </div>
        </div>

        {/* Identificación del equipo */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <h2 className="mb-6 text-xl font-semibold text-slate-900">Identificación</h2>
          
          <div className="space-y-6">
            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Nombre del equipo</Label>
                <Input
                  value={formData.name}
                  onChange={(e) => setFormData(prev => ({ ...prev, name: e.target.value }))}
                  placeholder="Ej: Electrocardiografo"
                />
              </div>
              <div className="space-y-2">
                <Label>Código inventario</Label>
                <Input
                  value={formData.inventoryCode}
                  onChange={(e) => setFormData(prev => ({ ...prev, inventoryCode: e.target.value }))}
                  placeholder="BIO-XXXXX"
                  className="font-mono"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Código IPS</Label>
                <Input
                  value={formData.ipsCode}
                  onChange={(e) => setFormData(prev => ({ ...prev, ipsCode: e.target.value }))}
                  placeholder="Código IPS"
                  className="font-mono"
                />
              </div>
              <div className="space-y-2">
                <Label>Código ECRI</Label>
                <Input
                  value={formData.ecriCode}
                  onChange={(e) => setFormData(prev => ({ ...prev, ecriCode: e.target.value }))}
                  placeholder="Código ECRI"
                />
              </div>
            </div>

            <div className="h-px bg-slate-200" />

            <div className="grid gap-6 md:grid-cols-3">
              <div className="space-y-2">
                <Label>Marca</Label>
                <Input
                  value={formData.brand}
                  onChange={(e) => setFormData(prev => ({ ...prev, brand: e.target.value }))}
                  placeholder="Marca del equipo"
                />
              </div>
              <div className="space-y-2">
                <Label>Modelo</Label>
                <Input
                  value={formData.model}
                  onChange={(e) => setFormData(prev => ({ ...prev, model: e.target.value }))}
                  placeholder="Modelo"
                />
              </div>
              <div className="space-y-2">
                <Label>Número de serie</Label>
                <Input
                  value={formData.serialNumber}
                  onChange={(e) => setFormData(prev => ({ ...prev, serialNumber: e.target.value }))}
                  placeholder="Serie"
                  className="font-mono"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-3">
              <div className="space-y-2">
                <Label>Fecha de fabricación</Label>
                <Input
                  type="date"
                  value={formData.manufactureDate}
                  onChange={(e) => setFormData(prev => ({ ...prev, manufactureDate: e.target.value }))}
                />
              </div>
              <div className="space-y-2">
                <Label>Vida útil (años)</Label>
                <Input
                  value={formData.usefulLife}
                  onChange={(e) => setFormData(prev => ({ ...prev, usefulLife: e.target.value }))}
                  placeholder="Ej: 10 años"
                />
              </div>
              <div className="space-y-2">
                <Label>Área</Label>
                <Input
                  value={formData.area}
                  onChange={(e) => setFormData(prev => ({ ...prev, area: e.target.value }))}
                  placeholder="Ej: Cardiología"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Responsable</Label>
                <Input
                  value={formData.responsible}
                  onChange={(e) => setFormData(prev => ({ ...prev, responsible: e.target.value }))}
                  placeholder="Nombre del responsable"
                />
              </div>
              <div className="space-y-2">
                <Label>Ubicación física</Label>
                <Input
                  value={formData.location}
                  onChange={(e) => setFormData(prev => ({ ...prev, location: e.target.value }))}
                  placeholder="Ej: Piso 3, Sala 201"
                />
              </div>
            </div>

            <div className="h-px bg-slate-200" />

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-3">
                <Label>Registro Invima</Label>
                <div className="flex gap-2">
                  {(['RS', 'PC', 'NR'] as InvimaType[]).map((option) => (
                    <button
                      key={option}
                      type="button"
                      onClick={() => setFormData(prev => ({ ...prev, invimaType: prev.invimaType === option ? null : option }))}
                      className={`flex-1 px-4 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                        formData.invimaType === option
                          ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                          : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                      }`}
                    >
                      {option}
                    </button>
                  ))}
                </div>
                <Input
                  value={formData.invimaCode}
                  onChange={(e) => setFormData(prev => ({ ...prev, invimaCode: e.target.value }))}
                  placeholder="Código de registro"
                  className="font-mono"
                />
              </div>

              <div className="space-y-3">
                <Label>Clasificación de riesgo</Label>
                <div className="grid grid-cols-4 gap-2">
                  {(['I', 'IIA', 'IIB', 'III'] as RiskClass[]).map((risk) => (
                    <button
                      key={risk}
                      type="button"
                      onClick={() => setFormData(prev => ({ ...prev, riskClass: prev.riskClass === risk ? '' : risk }))}
                      className={`px-4 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                        formData.riskClass === risk
                          ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                          : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                      }`}
                    >
                      {risk}
                    </button>
                  ))}
                </div>
              </div>
            </div>

            <div className="space-y-2">
              <Label>Componentes y accesorios</Label>
              <Textarea
                value={formData.components}
                onChange={(e) => setFormData(prev => ({ ...prev, components: e.target.value }))}
                placeholder="Lista de componentes, accesorios y observaciones adicionales"
                rows={3}
              />
            </div>
          </div>
        </div>

        {/* Adquisición */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <h2 className="mb-6 text-xl font-semibold text-slate-900">Adquisición</h2>
          
          <div className="space-y-6">
            <div className="space-y-3">
              <Label>Forma de adquisición</Label>
              <div className="grid grid-cols-2 md:grid-cols-5 gap-2">
                {(['Compra directa', 'Donación', 'Comodato', 'Alquiler', 'Otro'] as AcquisitionForm[]).map((option) => (
                  <button
                    key={option}
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, acquisitionForm: prev.acquisitionForm === option ? '' : option }))}
                    className={`px-3 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                      formData.acquisitionForm === option
                        ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                        : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                    }`}
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Tipo de documento</Label>
                <Input
                  value={formData.documentType}
                  onChange={(e) => setFormData(prev => ({ ...prev, documentType: e.target.value }))}
                  placeholder="Ej: Factura, Orden de compra"
                />
              </div>
              <div className="space-y-2">
                <Label>Número de documento</Label>
                <Input
                  value={formData.documentNumber}
                  onChange={(e) => setFormData(prev => ({ ...prev, documentNumber: e.target.value }))}
                  placeholder="Número"
                  className="font-mono"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-3">
              <div className="space-y-2">
                <Label>Fecha de adquisición</Label>
                <Input
                  type="date"
                  value={formData.acquisitionDate}
                  onChange={(e) => setFormData(prev => ({ ...prev, acquisitionDate: e.target.value }))}
                />
              </div>
              <div className="space-y-2">
                <Label>Cambio por garantía</Label>
                <Input
                  type="date"
                  value={formData.warrantyReplacementDate}
                  onChange={(e) => setFormData(prev => ({ ...prev, warrantyReplacementDate: e.target.value }))}
                />
              </div>
              <div className="space-y-2">
                <Label>Vencimiento garantía</Label>
                <Input
                  type="date"
                  value={formData.warrantyExpiration}
                  onChange={(e) => setFormData(prev => ({ ...prev, warrantyExpiration: e.target.value }))}
                />
              </div>
            </div>

            <div className="h-px bg-slate-200" />

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Proveedor</Label>
                <Input
                  value={formData.supplier}
                  onChange={(e) => setFormData(prev => ({ ...prev, supplier: e.target.value }))}
                  placeholder="Nombre del proveedor"
                />
              </div>
              <div className="space-y-2">
                <Label>NIT</Label>
                <Input
                  value={formData.supplierNIT}
                  onChange={(e) => setFormData(prev => ({ ...prev, supplierNIT: e.target.value }))}
                  placeholder="NIT del proveedor"
                  className="font-mono"
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label>Información de contacto</Label>
              <Textarea
                value={formData.supplierContact}
                onChange={(e) => setFormData(prev => ({ ...prev, supplierContact: e.target.value }))}
                placeholder="Nombre, teléfono, correo electrónico"
                rows={3}
              />
            </div>
          </div>
        </div>

        {/* Condiciones de funcionamiento */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <h2 className="mb-6 text-xl font-semibold text-slate-900">Condiciones de funcionamiento</h2>
          
          <div className="space-y-6">
            <div className="grid gap-6 md:grid-cols-4">
              <div className="space-y-2">
                <Label>Voltaje</Label>
                <Input
                  value={formData.voltage}
                  onChange={(e) => setFormData(prev => ({ ...prev, voltage: e.target.value }))}
                  placeholder="Ej: 110-220V"
                />
              </div>
              <div className="space-y-2">
                <Label>Corriente</Label>
                <Input
                  value={formData.current}
                  onChange={(e) => setFormData(prev => ({ ...prev, current: e.target.value }))}
                  placeholder="Ej: 5A"
                />
              </div>
              <div className="space-y-2">
                <Label>Humedad relativa</Label>
                <Input
                  value={formData.relativeHumidity}
                  onChange={(e) => setFormData(prev => ({ ...prev, relativeHumidity: e.target.value }))}
                  placeholder="Ej: 30-75%"
                />
              </div>
              <div className="space-y-2">
                <Label>Temperatura</Label>
                <Input
                  value={formData.temperature}
                  onChange={(e) => setFormData(prev => ({ ...prev, temperature: e.target.value }))}
                  placeholder="Ej: 15-30°C"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Dimensiones</Label>
                <Input
                  value={formData.dimensions}
                  onChange={(e) => setFormData(prev => ({ ...prev, dimensions: e.target.value }))}
                  placeholder="Ej: 50x40x30 cm"
                />
              </div>
              <div className="space-y-2">
                <Label>Peso</Label>
                <Input
                  value={formData.weight}
                  onChange={(e) => setFormData(prev => ({ ...prev, weight: e.target.value }))}
                  placeholder="Ej: 15 kg"
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label>Otros requisitos</Label>
              <Textarea
                value={formData.otherConditions}
                onChange={(e) => setFormData(prev => ({ ...prev, otherConditions: e.target.value }))}
                placeholder="Requisitos adicionales de funcionamiento"
                rows={2}
              />
            </div>
          </div>
        </div>

        {/* Datos metrológicos */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <h2 className="mb-6 text-xl font-semibold text-slate-900">Datos metrológicos</h2>
          
          <div className="space-y-6">
            <div className="grid gap-6 md:grid-cols-3">
              <div className="space-y-2">
                <Label>Magnitud de medida</Label>
                <Input
                  value={formData.measurementMagnitude}
                  onChange={(e) => setFormData(prev => ({ ...prev, measurementMagnitude: e.target.value }))}
                  placeholder="Ej: Presión, Temperatura"
                />
              </div>
              <div className="space-y-2">
                <Label>Rango del equipo</Label>
                <Input
                  value={formData.equipmentRange}
                  onChange={(e) => setFormData(prev => ({ ...prev, equipmentRange: e.target.value }))}
                  placeholder="Ej: 0-200 mmHg"
                />
              </div>
              <div className="space-y-2">
                <Label>Rango de uso</Label>
                <Input
                  value={formData.serviceUsageRange}
                  onChange={(e) => setFormData(prev => ({ ...prev, serviceUsageRange: e.target.value }))}
                  placeholder="Rango habitual"
                />
              </div>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-2">
                <Label>Resolución</Label>
                <Input
                  value={formData.resolution}
                  onChange={(e) => setFormData(prev => ({ ...prev, resolution: e.target.value }))}
                  placeholder="Ej: 0.1 mmHg"
                />
              </div>
              <div className="space-y-2">
                <Label>Error máximo permitido</Label>
                <Input
                  value={formData.maxAllowedError}
                  onChange={(e) => setFormData(prev => ({ ...prev, maxAllowedError: e.target.value }))}
                  placeholder="Ej: ±2%"
                />
              </div>
            </div>

            <div className="h-px bg-slate-200" />

            <div className="grid gap-6 md:grid-cols-2">
              <div className="space-y-3">
                <Label>Frecuencia de calibración</Label>
                <div className="flex gap-2">
                  {(['Anual', 'Semestral', 'Otro'] as MetrologicalFrequency[]).map((option) => (
                    <button
                      key={option}
                      type="button"
                      onClick={() => setFormData(prev => ({ 
                        ...prev, 
                        metrologicalFrequencyOption: prev.metrologicalFrequencyOption === option ? '' : option,
                        metrologicalFrequencyOther: prev.metrologicalFrequencyOption === option ? '' : prev.metrologicalFrequencyOther
                      }))}
                      className={`flex-1 px-4 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                        formData.metrologicalFrequencyOption === option
                          ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                          : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                      }`}
                    >
                      {option}
                    </button>
                  ))}
                </div>
                {formData.metrologicalFrequencyOption === 'Otro' && (
                  <Input
                    value={formData.metrologicalFrequencyOther}
                    onChange={(e) => setFormData(prev => ({ ...prev, metrologicalFrequencyOther: e.target.value }))}
                    placeholder="Especifica la frecuencia"
                  />
                )}
              </div>

              <div className="space-y-3">
                <Label>¿Requiere calibración?</Label>
                <div className="flex gap-2">
                  <button
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, requiresCalibration: true }))}
                    className={`flex-1 px-4 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                      formData.requiresCalibration
                        ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                        : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                    }`}
                  >
                    Sí
                  </button>
                  <button
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, requiresCalibration: false }))}
                    className={`flex-1 px-4 py-2.5 rounded-xl border-2 text-sm font-medium transition ${
                      !formData.requiresCalibration
                        ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                        : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                    }`}
                  >
                    No
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Historial metrológico */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <div className="mb-6 flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-900">Historial metrológico</h2>
            <Button
              type="button"
              variant="outline"
              size="sm"
              onClick={addHistoryRecord}
            >
              <Plus className="h-4 w-4" />
              Agregar
            </Button>
          </div>
          
          {formData.metrologicalHistory.length === 0 ? (
            <div className="rounded-xl bg-slate-50 border border-slate-200 p-8 text-center">
              <p className="text-sm text-slate-500">
                No hay registros. Haz clic en "Agregar" para comenzar.
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              {formData.metrologicalHistory.map((record, index) => (
                <div key={index} className="rounded-xl border border-slate-200 bg-slate-50/50 p-6 space-y-4">
                  <div className="grid gap-4 md:grid-cols-3">
                    <div className="space-y-2">
                      <Label className="text-xs">Fecha</Label>
                      <Input
                        value={record.date}
                        onChange={(e) => updateHistoryRecord(index, 'date', e.target.value)}
                        placeholder="DD/MM/AAAA"
                        className="bg-white"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label className="text-xs">N° informe</Label>
                      <Input
                        value={record.reportNumber}
                        onChange={(e) => updateHistoryRecord(index, 'reportNumber', e.target.value)}
                        placeholder="Número"
                        className="bg-white font-mono"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label className="text-xs">Proveedor</Label>
                      <Input
                        value={record.supplier}
                        onChange={(e) => updateHistoryRecord(index, 'supplier', e.target.value)}
                        placeholder="Proveedor"
                        className="bg-white"
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label className="text-xs">Actividad</Label>
                    <div className="flex gap-2">
                      {(['MP', 'MC', 'C'] as MetrologicalActivity[]).map((activity) => (
                        <button
                          key={activity}
                          type="button"
                          onClick={() => toggleHistoryActivity(index, activity)}
                          className={`flex-1 px-3 py-2 rounded-lg border-2 text-sm font-medium transition ${
                            record.activity.includes(activity)
                              ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                              : 'border-slate-200 bg-white text-slate-600 hover:border-emerald-200'
                          }`}
                        >
                          {activity}
                        </button>
                      ))}
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label className="text-xs">Observaciones</Label>
                    <Textarea
                      value={record.observations}
                      onChange={(e) => updateHistoryRecord(index, 'observations', e.target.value)}
                      placeholder="Observaciones del registro"
                      rows={2}
                      className="bg-white"
                    />
                  </div>

                  <div className="flex justify-end pt-2">
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      onClick={() => removeHistoryRecord(index)}
                      className="text-red-600 hover:text-red-700 hover:bg-red-50"
                    >
                      <Trash2 className="h-4 w-4" />
                      Eliminar
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Observaciones */}
        <div className="rounded-2xl border border-slate-200 bg-white p-8 shadow-sm">
          <div className="mb-6 flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-900">Observaciones adicionales</h2>
            <Button
              type="button"
              variant="outline"
              size="sm"
              onClick={addObservation}
            >
              <Plus className="h-4 w-4" />
              Agregar
            </Button>
          </div>
          
          {formData.observations.length === 0 ? (
            <div className="rounded-xl bg-slate-50 border border-slate-200 p-8 text-center">
              <p className="text-sm text-slate-500">
                No hay observaciones. Haz clic en "Agregar" para comenzar.
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              {formData.observations.map((observation, index) => (
                <div key={index} className="rounded-xl border border-slate-200 bg-slate-50/50 p-6 space-y-4">
                  <div className="grid gap-4 md:grid-cols-2">
                    <div className="space-y-2">
                      <Label className="text-xs">Fecha</Label>
                      <Input
                        value={observation.date}
                        onChange={(e) => updateObservation(index, 'date', e.target.value)}
                        placeholder="DD/MM/AAAA"
                        className="bg-white"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label className="text-xs">Responsable</Label>
                      <Input
                        value={observation.name}
                        onChange={(e) => updateObservation(index, 'name', e.target.value)}
                        placeholder="Nombre"
                        className="bg-white"
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label className="text-xs">Observación</Label>
                    <Textarea
                      value={observation.observation}
                      onChange={(e) => updateObservation(index, 'observation', e.target.value)}
                      placeholder="Detalle de la observación"
                      rows={2}
                      className="bg-white"
                    />
                  </div>

                  <div className="flex justify-end pt-2">
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      onClick={() => removeObservation(index)}
                      className="text-red-600 hover:text-red-700 hover:bg-red-50"
                    >
                      <Trash2 className="h-4 w-4" />
                      Eliminar
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Botones de acción */}
        <div className="flex items-center justify-end gap-3 pb-8">
          <Button type="button" variant="outline" size="lg">
            Cancelar
          </Button>
          <Button type="submit" size="lg">
            Guardar equipo
          </Button>
        </div>
      </form>
    </div>
  )
}
